
import React from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter } from "react-router-dom";
import App from "./App";
import "./index.css";
import { fixProjectDates } from "./utils/storageUtils";
import { userService } from "./services/auth/userService";

//      
window.onerror = function(message, source, lineno, colno, error) {
  console.error(" :", { message, source, lineno, colno, error });
  
  //      
  const errorContainer = document.getElementById('global-error');
  if (errorContainer) {
    errorContainer.style.display = 'block';
    errorContainer.textContent = ` : ${message}. ,  .`;
  }
  
  return false;
};

//   
const initializeApp = () => {
  try {
    console.log(" ...");
    
    //  
    userService.initializeDefaultUsers();
    console.log(" ");
    
    //      
    try {
      console.log("   ...");
      fixProjectDates();
      console.log("  ");
    } catch (error) {
      console.error("    :", error);
    }
    
    console.log(" React ...");
    
    //   
    const rootElement = document.getElementById("root");
    
    if (!rootElement) {
      throw new Error("  #root  ");
    }
    
    //   React   
    const root = ReactDOM.createRoot(rootElement);
    
    root.render(
      <React.StrictMode>
        <BrowserRouter>
          <App />
        </BrowserRouter>
      </React.StrictMode>
    );
    
    console.log("  ");
  } catch (error) {
    console.error("    :", error);
    
    //     
    const rootElement = document.getElementById("root");
    if (rootElement) {
      rootElement.innerHTML = `
        <div style="text-align: center; padding: 20px; font-family: sans-serif;">
          <h2>    </h2>
          <p>,      .</p>
          <p style="color: #666; font-size: 0.8em;">: ${error instanceof Error ? error.message : String(error)}</p>
          <button onclick="window.location.reload()" style="padding: 8px 16px; margin-top: 10px;">
             
          </button>
        </div>
      `;
    }
  }
};

//  div    
const errorDiv = document.createElement('div');
errorDiv.id = 'global-error';
errorDiv.style.display = 'none';
errorDiv.style.position = 'fixed';
errorDiv.style.top = '0';
errorDiv.style.left = '0';
errorDiv.style.right = '0';
errorDiv.style.padding = '10px';
errorDiv.style.backgroundColor = '#f44336';
errorDiv.style.color = 'white';
errorDiv.style.textAlign = 'center';
errorDiv.style.zIndex = '9999';
document.body.appendChild(errorDiv);

//      DOM
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', initializeApp);
} else {
  initializeApp();
}
