
import { User } from "@/types/project";

/**
 *     
 *   CRUD   
 */

//      localStorage
const USERS_STORAGE_KEY = "users";

export const userService = {
  /**
   *     ,
   *     
   */
  initializeDefaultUsers(): void {
    try {
      // ,      
      const existingUsers = this.getUsersFromStorage();
      
      //        ,   
      if (!existingUsers || !Array.isArray(existingUsers) || existingUsers.length === 0) {
        const defaultUsers: User[] = [
          {
            id: "default-manager",
            name: "",
            email: "manager",
            password: "manager123",
            role: "manager",
          },
          {
            id: "default-employee",
            name: "",
            email: "employee",
            password: "employee123",
            role: "employee",
          },
        ];
        
        //    
        localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(defaultUsers));
        console.log("   ");
      }
    } catch (error) {
      console.error("   :", error);
      //         
      this.resetUsers();
    }
  },

  /**
   *       
   */
  resetUsers(): void {
    try {
      const defaultUsers: User[] = [
        {
          id: "default-manager",
          name: "",
          email: "manager",
          password: "manager123",
          role: "manager",
        },
        {
          id: "default-employee",
          name: "",
          email: "employee",
          password: "employee123",
          role: "employee",
        },
      ];
      
      localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(defaultUsers));
      console.log("    ");
    } catch (error) {
      console.error("   :", error);
    }
  },

  /**
   *      
   * @returns        
   */
  getUsersFromStorage(): User[] {
    try {
      const usersJson = localStorage.getItem(USERS_STORAGE_KEY);
      if (usersJson) {
        const users = JSON.parse(usersJson);
        if (Array.isArray(users)) {
          return users;
        }
      }
      return [];
    } catch (error) {
      console.error("   :", error);
      return [];
    }
  },

  /**
   *   
   * @param user -   
   * @returns    null   
   */
  addUser(user: User): User | null {
    try {
      const users = this.getUsersFromStorage();
      
      // ,     email   
      const existingUser = users.find(u => u.email === user.email);
      if (existingUser) {
        console.error("   email  ");
        return null;
      }
      
      //   
      users.push(user);
      localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(users));
      
      return user;
    } catch (error) {
      console.error("   :", error);
      return null;
    }
  },

  /**
   *    ID
   * @param id - ID 
   * @returns   null,    
   */
  getUserById(id: string): User | null {
    try {
      const users = this.getUsersFromStorage();
      return users.find(user => user.id === id) || null;
    } catch (error) {
      console.error("     ID:", error);
      return null;
    }
  },

  /**
   *   
   * @param id - ID 
   * @param userData -   
   * @returns    null   
   */
  updateUser(id: string, userData: Partial<User>): User | null {
    try {
      const users = this.getUsersFromStorage();
      const userIndex = users.findIndex(user => user.id === id);
      
      if (userIndex === -1) {
        console.error("  ");
        return null;
      }
      
      //   
      users[userIndex] = { ...users[userIndex], ...userData };
      localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(users));
      
      return users[userIndex];
    } catch (error) {
      console.error("   :", error);
      return null;
    }
  },

  /**
   *  
   * @param id - ID 
   * @returns true   , false   
   */
  deleteUser(id: string): boolean {
    try {
      const users = this.getUsersFromStorage();
      const filteredUsers = users.filter(user => user.id !== id);
      
      if (filteredUsers.length === users.length) {
        console.error("  ");
        return false;
      }
      
      localStorage.setItem(USERS_STORAGE_KEY, JSON.stringify(filteredUsers));
      return true;
    } catch (error) {
      console.error("   :", error);
      return false;
    }
  },
};
