//    
export interface UserSession {
  id: string;
  username: string;
  role: "manager" | "employee";
  isAuthenticated: boolean;
  loginTime: string;
}

//      localStorage
const SESSION_KEY = "user";
//      
const ERROR_MESSAGE_KEY = "auth_message";

export const sessionService = {
  /**
   *     localStorage
   * @param session -  
   */
  saveSession(session: UserSession): void {
    try {
      localStorage.setItem(SESSION_KEY, JSON.stringify(session));
    } catch (error) {
      console.error("   :", error);
    }
  },

  /**
   *      localStorage
   * @returns    null,    
   */
  getCurrentSession(): UserSession | null {
    try {
      const sessionJson = localStorage.getItem(SESSION_KEY);
      if (sessionJson) {
        return JSON.parse(sessionJson);
      }
      return null;
    } catch (error) {
      console.error("   :", error);
      return null;
    }
  },

  /**
   *    
   */
  clearSession(): void {
    try {
      localStorage.removeItem(SESSION_KEY);
    } catch (error) {
      console.error("   :", error);
    }
  },

  /**
   *         
   * @param message -   
   */
  saveErrorMessage(message: string): void {
    sessionStorage.setItem(ERROR_MESSAGE_KEY, message);
  },

  /**
   *         sessionStorage
   * @returns     null,    
   */
  getErrorMessage(): string | null {
    const message = sessionStorage.getItem(ERROR_MESSAGE_KEY);
    if (message) {
      sessionStorage.removeItem(ERROR_MESSAGE_KEY);
      return message;
    }
    return null;
  },
};
